"use client"

import { useState } from "react"
import { Header } from "./finance/header"
import { SummaryCards } from "./finance/summary-cards"
import { StatisticsChart } from "./finance/statistics-chart"
import { TransactionTabs } from "./finance/transaction-tabs"
import type { Transaction, TransactionType } from "@/lib/finance-data"
import { initialTransactions, months } from "@/lib/finance-data"

export default function FinanceTracker() {
  const [selectedMonth, setSelectedMonth] = useState("জানুয়ারি ২০২৫")
  const [transactions, setTransactions] = useState<Transaction[]>(initialTransactions)

  const addTransaction = (transaction: Omit<Transaction, "id">) => {
    const newTransaction: Transaction = {
      ...transaction,
      id: Date.now().toString(),
    }
    setTransactions([newTransaction, ...transactions])
  }

  const deleteTransaction = (id: string) => {
    setTransactions(transactions.filter((t) => t.id !== id))
  }

  const editTransaction = (id: string, updatedTransaction: Omit<Transaction, "id">) => {
    setTransactions(transactions.map((t) => (t.id === id ? { ...updatedTransaction, id } : t)))
  }

  const getFilteredTransactions = (type: TransactionType) => {
    return transactions.filter((t) => t.type === type && t.month === selectedMonth)
  }

  const getTotals = () => {
    const monthTransactions = transactions.filter((t) => t.month === selectedMonth)
    const income = monthTransactions.filter((t) => t.type === "income").reduce((sum, t) => sum + t.amount, 0)
    const expense = monthTransactions.filter((t) => t.type === "expense").reduce((sum, t) => sum + t.amount, 0)
    const savings = monthTransactions.filter((t) => t.type === "savings").reduce((sum, t) => sum + t.amount, 0)
    return { income, expense, savings }
  }

  const totals = getTotals()

  return (
    <div className="container mx-auto px-4 py-6 max-w-7xl">
      <Header selectedMonth={selectedMonth} onMonthChange={setSelectedMonth} months={months} />

      <SummaryCards totals={totals} />

      <StatisticsChart totals={totals} />

      <TransactionTabs
        incomeTransactions={getFilteredTransactions("income")}
        expenseTransactions={getFilteredTransactions("expense")}
        savingsTransactions={getFilteredTransactions("savings")}
        onAdd={addTransaction}
        onDelete={deleteTransaction}
        onEdit={editTransaction}
        selectedMonth={selectedMonth}
      />
    </div>
  )
}
